import * as THREE from 'three';
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls"


const loader = new THREE.TextureLoader();
const scene = new THREE.Scene();
const camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
const renderer = new THREE.WebGLRenderer();

renderer.setSize(window.innerWidth, window.innerHeight);
document.body.appendChild(renderer.domElement);

const geometry = new THREE.BoxGeometry(1,1,1);

const tmp1 = require("./normaltex.png")
//const normalTexture = loader.load("normaltex.png");
const normalTexture = loader.load(tmp1);
console.log(normalTexture)

const tmp2 = require("./roughnesstex.png")
//const roughnessTexture = loader.load("roughnesstex.png");
const roughnessTexture = loader.load(tmp2);
console.log(roughnessTexture)

const tmp3 = require("./aotex.png")
//const aoTexture = loader.load("aotex.png");
const aoTexture = loader.load(tmp3);
console.log(aoTexture)

const tmp4 = require("./colortex.png")
//const colorTexture = loader.load("colortex.png")
const colorTexture = loader.load(tmp4)
console.log(colorTexture)

const material = new THREE.MeshStandardMaterial({ 
	//color: 0xFFffFF,
	map: colorTexture,
	normalMap: normalTexture,
	aoMap: aoTexture,
	roughnessMap: roughnessTexture
	});

//const material = new THREE.MeshStandardMaterial({color: '#FFFFFF', roughness: 0.0, metalness: 0.9})

const cube = new THREE.Mesh(geometry, material);

scene.add(cube);

camera.position.z = 10;

//light
const amlight = new THREE.AmbientLight( 0xFFFFFF,100 );
//amlight.position.set(10,10,6)
amlight.visible = true;
scene.add( amlight );

const splight = new THREE.SpotLight(0xFFFFFF)
splight.position.set(3,3,3)
splight.visible = true;
splight.castShadow = true;
scene.add( splight );


const controls = new OrbitControls( camera, renderer.domElement );


const animate = function () {
    requestAnimationFrame(animate);
    cube.rotation.x += 0.01;
    cube.rotation.y += 0.01;
    controls.update();
    renderer.render(scene, camera);
};

animate();
